.notification-card-listing {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 28px;
  padding-left: 2px;

  .notification-card {
    padding: 20px 0;
    border-bottom: 1px solid #232b58;

    &:first-child {
      padding: 0 0 20px;
    }

    &:last-child {
      border-bottom: none;
    }
  }
}