.order-information-wrapper {
  .after-heading-wrapper {
    padding-top: 0;
  }

  .timer-block {
    padding: 40px 50px;

    background-color: #060E38;
  }

  .timer-wrapper {
    position: relative;

    .time-values {
      display: flex;
      justify-content: space-around;

      p {
        font-size: 45px;
        margin-bottom: 0px;
      }

      div {
        flex: 0 0 32%;
      }
    }

    .time-units {
      display: flex;
      justify-content: space-around;
    }
  }

  .time-values-duplicate,
  .time-units-duplicate {
    display: none;
  }

  .detail-block {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    height: 100%;
  }

  .btn-router-proceed {
    padding: 17px 15px;

    color: $white;

    background-color: $pink;
    box-shadow: 0px 6px 13px 0px rgba(253, 34, 129, 0.44);
    border: 2px solid transparent;
    border-radius: 30px;

    &:hover {
      color: $pink;

      background-color: $white;
      border: 2px solid $dim-gray;
    }
  }

  .btn-reject {
    margin: 15px 0;

    font-weight: bold;
    color: $white;
    opacity: 1;

    &:hover {
      opacity: .5;
    }
  }
}

