.section-profile {
  background: rgba($color: $navy, $alpha: .9);

  .after-heading-wrapper {
    background: $hot-violet;
  }
}

.profile-data {
  display: flex;
  align-items: center;
  padding: 30px;

  background: $darkened-violet;
}

.profile-image-wrapper {
  margin-right: 20px;
  position: relative;
  z-index: 1;

  &.with-notif {
    &:before {
      content: '';

      position: ABSOLUTE;
      z-index: 1;
      right: 0px;
      bottom: 5px;

      width: 15px;
      height: 15px;

      background: $sunflower;
      border-radius: 50px;
    }
  }
}

.user-name {
  margin-bottom: 5px;

  // font-family: $font-codec-cold-trial-bold;
  font-size: 18px;
  color: $white;
}

.link-wrapper,
.profile-link {
  font-family: $font-poppins-regular;
  font-size: 14px;
  color: $bright-pink;
}

.profile-link {
  text-decoration: underline;

  &:hover {
    color: $bright-pink;

    opacity: .5;
  }

  &:first-child {
    margin-right: 5px;
  }

  &:last-child {
    margin-left: 5px;
  }
}
