.detail-wrapper {
  background-color: $navy;

  .fan-questions {
    margin-top: 0;
  }

  
  .btn-round-white {
    background: #fff;
    border-radius: 50%;
    border: none;
    width: 70px;
    height: 70px;
    padding: 22px 20px 20px;
    box-shadow: 0 0 11px #6817ff;
    margin-top: -31px;

    img {
      max-width: 41px;
      display: flex;
      margin-top: -5px;
      justify-content: center;
      margin-left: -3px;
    }
  }

  [id="heart"] {
    position: absolute;
    left: -100vw;
  }

  [for="heart"] {
    color: #aab8c2;
    cursor: pointer;
    align-self: center;
    transition: color 0.2s ease-in-out;
    margin-bottom: 0;
  }

  [for="heart"]:hover {
    opacity: .5;
  }

  [for="heart"]::selection {
    color: none;
    background: transparent;
  }

  [for="heart"]::moz-selection {
    color: none;
    background: transparent;
  }

  [id="heart"]:checked + label img {
    opacity: .5;
  }

  .badge {
    font-family: $font-codec-cold-regular-trial;
    font-size: 13px;
  }

  .badge-one {
    color: #925afb;
  }

  .badge-two {
    color: #ffb4cc;
  }

  .pills-section {
    margin-bottom: 5px;
  }

  .pills-wrapper {
    display: flex;
    justify-content: center;
    padding: 0 10px;
    margin-bottom: 10px;
  }

  .pill {
    padding: 5px 15px;

    font-family: $font-poppins-regular;
    font-size: 18px;
    color: $white;
    text-align: center;

    &:hover {
      color: $white;
      text-decoration: none;
    }

    &.pill-purple {
      flex: 0 0 37%;
      background-color: $grape;
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;

      .pill-text {
        &:first-child {
          margin-left: 5px;
        }
      }
    }

    &.pill-pink {
      flex: 0 0 57%;
      background-color: $pink;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
    }
  }

  .pill-text {
    display: inline-block;
    margin-bottom: 0;
  }

  .question-tick {
    width: 30px;
    height: 23px;
  }

  .idol-message {
    padding-bottom: 15px;
    margin-bottom: 10px;

    border-bottom: 2px solid $fiord;
  }

  .detail-paragraph {
    // padding: 0 10px;
    margin-bottom: 6px;

    font-family: $font-poppins-regular;
    font-size: 13px;
  }

  .idol-banner {
    position: relative;
  }
  
  .idol-image {
    width: 100%;
    height: 450px;
    object-fit: cover;
    border-radius: 13em/8em;
    border-top-right-radius: unset;
    border-top-left-radius: unset;
    box-shadow: 0 1px 15px rgba(104, 23, 255, .6);
  }
  
  .back-arrow {
    position: absolute;
    top: 20px;
    left: 20px;
  }
  
  .idol-name {
    margin-top: 7px;
    margin-bottom: 12px;
  
    font-family: $font-codec-cold-regular-trial;
    font-size: 29px;
  }
  
  .idol-category {
    margin-bottom: 12px;
  }
}

.action-list {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  margin-top: -10px;
}

.action-icon {
  width: 25px;

  .heart {
    height: 25px;
  }

  .share {
    height: 26px;
  }
}

@include respond-to(xs) {
  .detail-wrapper {
    .pills-wrapper {
      padding: 0;
    }

    .pill {
      padding: 5px 0;
    }
  }
}