footer {
  position: fixed;
  bottom: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;

  text-align: center;
}

.footer-text {
  font-size: 12px;
  font-family: $font-poppins-regular;
  color: $white;
}

.footer-link {
  color: $purple;
  text-decoration: underline;
}