.profile-update-wrapper {
  .after-heading-wrapper {
    padding-top: 30px;
  }

  .round-image {
    width: 150px;
    height: 150px;
    border-radius: 100%;
  }

  .profile-image-wrapper {
    display: flex;
    justify-content: center;
    position: relative;
    max-width: 300px;
    margin: 0 auto 30px;
  }

  .dropzone {
    background: white;
    width: 70px;
    height: 70px;
    display: flex;
    border-radius: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 40px;
    right: 36px;
    justify-content: center;
    cursor: pointer;
  }

  .dropzone-data {
    margin: 0 auto;
  }

  .update-form-wrapper {
    padding-bottom: 73px;
  }

  .phone-holder {
    display: flex;
    justify-content: space-between;
  }

  .select-holder {
    flex: 0 0 15%;
  }

  .phone-input {
    flex: 0 0 83%;
  }

  .textbox {
    font-size: 15px;
  }

  .textbox,
  .title-textbox {
    font-family: $font-poppins-regular;
    color: $white;
  }
}